@font-face {
  font-family: "Tajawal";
  src: url(./fonts/Tajawal-Regular.ttf);
}
* {
  font-family: "Tajawal", sans-serif !important;
}
.page_container {
  width: 100%;
  height: 100%;
}

.full_image {
  width: 100%;
  height: 100%;
  position: relative;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex {
  display: flex;
}

.alignItem {
  align-items: center;
}

cursor {
  cursor: pointer;
}

.question_card {
  width: 700px;
  height: 500px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  color: rgba(0, 0, 0, 0.87);
  /* transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms; */
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
  transition: all 0.5s ease;
  overflow: auto;
}

.header {
  height: 40px;
  border-block: 1px solid lightgray;
}
.header p {
  font-size: 15px;
  font-weight: bold;
  color: #1e2226;
}

.black_color {
  color: #1e2226;
}

.ques_btn {
  background-color: #4d9ee5;
  color: #fff;
  border: none;
  margin-inline-start: 5px;
  width: 100px;
  height: 32px;
}

.round {
  position: relative;
}

.round label {
  background-color: #fff;
  border: 1px solid #272d3b;
  border-radius: 50%;
  cursor: pointer;
  height: 28px;
  left: 0;
  position: absolute;
  top: 0;
  width: 28px;
}

.round label:after {
  border: 2px solid #fff;
  border-top: none;
  border-right: none;
  content: "";
  height: 6px;
  left: 7px;
  opacity: 0;
  position: absolute;
  top: 8px;
  transform: rotate(-45deg);
  width: 12px;
}

.round input[type="checkbox"] {
  visibility: hidden;
}

.round input[type="checkbox"]:checked + label {
  background-color: #12a9cc;

  border-color: #12a9cc;
  box-shadow: 2.5px 2.5px 5px 0px #a6abbd;

  box-shadow: -2.5px -2.5px 5px 0px #fafbff;
  filter: drop-shadow(-2.5px -2.5px 5px #fafbff),
    drop-shadow(2.5px 2.5px 5px #a6abbd);
}

.round input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

.question_label {
  margin-inline-start: 30px;
}
